import React from 'react';
import Navbar from '../Navbar';
import Footer from '../Footer';
import './ONama.css';

function ONama() {
  
  window.scrollTo(0, 0);
  
  return (
    <div>
      <Navbar/>
        <img src = './images/onama.jpg' alt="O Nama"/>
        <br/><br/><br/><br/><br/><br/>
        
        <h2 className='text'>O Nama</h2>
        <br/><br/><br/><br/><br/><br/>

        <p className='about-us-paragraph'>Ueniki Web Servis je ideja koju smo mi pojedinci iz 4.a razreda Strukovne kole Vice Vlatkovia osmislili kako bi ubrzali i olakali proces zapoljavanje i isplate uenika koji rade sezonski. Cilj smanjivanje redova i olakano i ubrzano plaanje.</p>
        <br/><br/><br/><br/><br/><br/>
        <Footer/>
    </div>
  )
  
}


export default ONama;
